import { SpecPage } from '@stencil/core/testing';
import { getHTMLElement } from '../utility/helper';
import * as el from './element-helpers';
import * as helper from '../utility/helper';

export async function openMenu(page: SpecPage) {
  page.root?.querySelector('phn-burger-button')?.click();
  await page.waitForChanges();
}

export async function openMenuFromMyPorscheIcon(page: SpecPage) {
  const myPorscheButton = helper.getHTMLElement(page.root?.querySelector('phn-my-porsche-icon-button'));
  myPorscheButton?.click();
  await page.waitForChanges();
}

export async function closeMenuFromCloseButton(page: SpecPage) {
  getHTMLElement(page.root?.querySelector('.close-button'))?.click();
  await page.waitForChanges();
}

export async function clickOnBackButton(page: SpecPage) {
  getHTMLElement(page.root?.querySelector('phn-back-button'))?.click();
  await page.waitForChanges();
}

export async function closeMenuFromEscapeKey(page: SpecPage) {
  el.keyDown(page.root ?? null, 'Escape');
  await page.waitForChanges();
}

export function getHTMLElementFromPage(page: SpecPage, selector: string): HTMLElement | null {
  return getHTMLElement(page.root?.querySelector(selector));
}

export async function waitForFocusTimeout() {
  await new Promise((resolve) => {
    setTimeout(resolve, 200);
  });
}
